/* Quote Block Editor Styles */

.wp-block-eara-quote {
  &.wp-block-eara-quote--white {
    background-color: #ffffff;
  }

  &.wp-block-eara-quote--light-blue {
    background-color: #f0f4ff;
  }

  &.wp-block-eara-quote--light-green {
    background-color: #f0fff4;
  }

  &.wp-block-eara-quote--light-gray {
    background-color: #f7f7f7;
  }
}
